import React from 'react'
import Zewa from '../../img/zewa.png'
import logoGame from '../../img/logoGame.png'
import './index.scss'
import bridge from '@vkontakte/vk-bridge';
import { connect } from 'react-redux'
import { startGame, showRules, showRes } from '../../store/actions'

class Menu extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            name: 'user'
        }
        bridge.subscribe((e) => console.log(e));
        bridge.send("VKWebAppGetUserInfo");
    }
    render() {
        const { startGame, showRules, showRes } = this.props
        console.log(this.props)
        return (
            <section className="menu">
                <div className="row justify-content-center nav">
                    <div className="col-lg-3">
                        <img src={Zewa} alt="" />
                    </div>
                    <div className="col-lg-7">
                        <img src={logoGame} alt="" />
                    </div>
                </div>
                <div className="row justify-content-center infoData">
                    <div className="col-lg-9">
                        <p>
                            Привет, <span>{this.state.name}</span>. Играй и получай скидки до <span className="special">70%</span><br />
                            на покупку продукции Zewa в магазинах Магнит.
                            Пройди все квесты и участвуй в розыгрыше подарочного купона Магнит на <span className="special">3000 рублей</span>.
                        </p>
                    </div>
                </div>
                <div className="row justify-content-center navigation">
                    <div className="col-lg-6">
                        <div className="row justify-content-center">
                            <div className="col-md-auto">
                                <button className="selectionBtn pink" onClick={() => this.props.startGame()}>Начать игру</button>
                            </div>
                        </div>
                        <div className="row justify-content-center">
                            <div className="col-md-auto">
                                <button className="selectionBtn" onClick={() => showRes()}>Рекорды</button>
                            </div>
                        </div>
                        <div className="row justify-content-center">
                            <div className="col-md-auto">
                                <button className="selectionBtn" onClick={() => showRules()}>Правила</button>
                            </div>
                        </div>
                        <div className="row justify-content-center">
                            <div className="col-md-auto">
                                <button className="selectionBtn">Выход</button>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}
const mapStateToProps = state => {
    return {
        rules: state.store.rules,
        start: state.store.start,
        results: state.store.results
    }
}

const mapDispatchToProps = dispatch => {
    return {
        startGame: () => dispatch(startGame()),
        showRules: () => dispatch(showRules()),
        showRes: () => dispatch(showRes())
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(Menu)