import React from 'react'
import Menu from './components/MenuComponent'
import SelectQwest from './components/SelectQwest'
import LoadingComponent from './components/LoadingComponent'
import './App.scss'
import logo from './img/zewa.png'
import { connect } from 'react-redux'
import { startGame, showRules, showRes } from './store/actions'

class App extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			loaded: false
		}
	}
	componentDidMount() {
		setTimeout(() => {
			this.setState({
				loaded: true
			})
		}, 3000)
	}
	render() {
		const { rules, start, results } = this.props
		const { loaded } = this.state
		return (
			<div className="gameContainer">
				{/* <nav className="row justify-content-between">
					<div className="col-lg-auto logo">
						<img src={logo} />
					</div>
					<div className="col-lg-auto delo">
						<span>Delo Z</span>
					</div>
				</nav> */}
				{start ?
					<SelectQwest /> :
					loaded ?
						<Menu /> :
						<LoadingComponent />
					// : <></>
				}
			</div>
		)

	}
}
const mapStateToProps = state => {
	return {
		rules: state.store.rules,
		start: state.store.start,
		results: state.store.results
	}
}

const mapDispatchToProps = dispatch => {
	return {
		startGame: () => dispatch(startGame()),
		showRules: () => dispatch(showRules()),
		showRes: () => dispatch(showRes())
	}
}


export default connect(mapStateToProps, mapDispatchToProps)(App)