import {
    START_GAME,
    SHOW_RULES,
    SHOW_RESULTS
} from './actionTypes'


export const startGame = () => ({
    type: START_GAME,
    payload: true
})
export const showRules = () => ({
    type: SHOW_RULES,
    payload: true
})
export const showRes = () => ({
    type: SHOW_RESULTS,
    payload: true
})